@media only screen and (max-width: 1024px) {
  .nav:before {
    content: "≡";
    font-size: 2em;
    color: #fff;
    margin-top: -10px;
  }
  .nav__list, .nav__item {
    display: none;
  }
  .nav:hover .nav__list, .nav:hover .nav__item {
    display: flex;
    flex-direction: column;
  }
  .nav {
    display: flex;
    flex-direction: column;
    right: 100px;
    position: absolute;
  }
  .nav__item {
    padding: 5px 12px;
  }
  .nav__item a {
    font-size: 25px;
    margin: 5px 0;
  }
  .nav__list .nav__item:nth-child(3) {
    border-right: none;
  }
  .header__slide {
    margin-right: 150px;
  }
}

@media only screen and (max-width: 900px) {
  .nav {
    right: 0px;
  }
}

@media only screen and (max-width: 775px) {
  .nav {
    left: 30px;
    top: 150px;
  }
  .nav__item a {
    font-size: 30px;
    margin: 15px 5px;
